import React, { useCallback, useEffect, useState } from 'react'
import * as logosmall from '../../assets/Clean Walk/Loading_Screen/Search.logo.small.jpg.png'
import * as midlogo from '../../assets/images/Loading_Screen/LD_CSF_Mid_logo.png'
import * as DULTlogo from '../../assets/images/Loading_Screen/LD_DULT_Logo.png'
import * as BSFlogo from '../../assets/images/Loading_Screen/LD_BSF_logo.png'
import * as divideLine from '../../assets/images/Loading_Screen/LD_divideLine.png'
import * as SGlogo from '../../assets/images/Loading_Screen/LD_SG_Logo.png'
import * as bgBelow from '../../assets/Clean Walk/Levels_Screen/BG_bottom.png'
import * as XarpieLabs from '../../assets/Clean Walk/Loading_Screen/Xarpie Labs.png'
import * as mediaCannibal from '../../assets/Clean Walk/Loading_Screen/media.cannibal.png'
import './style.css'
import { useHistory } from 'react-router-dom'
import Spinner from 'components/Common/Spinner'
import { loader, SyncData } from 'components/Common/core'
import IntroText from 'components/Common/IntroText'
const LoadingScreen = () => {
  const history = useHistory();
  const [spinner, setspinner] = useState(true)
  const [introslide, setintroslide] = useState(true)
  const firstTimeloader = () => {
    setTimeout(
      () => {
        history.replace('/StartScreen')
      }, 8000
    );
  }
  useEffect(() => {
    // if (localStorage.getItem('isFirsttime') === null) {
    //   setintroslide(true)
    // } else {
    //   setintroslide(false)
    // }
    // setTimeout(() => {
    //   setspinner(false)
    // }, 3000);
    // setTimeout(
    //   () => {
    //     if (localStorage.getItem('isFirsttime') === null) {
    //       loader()
    //       history.replace('/StartScreen')
    //     } else {
    //       SyncData()
    //       history.replace('/TaskListScreen')
    //     }
    //   }, 8000
    // );
  }, [])

  return (
    <>
      {/* {spinner ?
        <div style={{
          width: '100vw',
          height: '100vh',
          position: 'absolute',
          top: '0',
          zIndex: 20,
          backgroundColor: '#00243a',
        }}>
          <Spinner />
        </div> :

        null
      } */}
      {/* {introslide ?
        <div style={{
          width: '100vw',
          height: '100vh',
          position: 'absolute',
          top: '0',
          zIndex: 10,
          backgroundColor: '#00243a',
        }}>
          <IntroText hidePopup={(data: boolean) => {
            setintroslide(data)
            firstTimeloader()
          }} />
        </div> : null
      } */}
      <div onClick={() => {
        if (localStorage.getItem('isFirsttime') === null) {
          loader()
          history.replace('/StartScreen')
        } else {
          history.replace('/TaskListScreen')
        }
      }}>
        <div style={{ height: "100vh" }}>
          <div className='loadrow1'>
            <div className={"load-containertop"}>

              <div className="load-sub_containertop">
                <div className='top_innerrow'>

                  <div className="loadsubrow">
                    <div className='loadsubrowcolumn1'>
                      <img className='loadDULTlogo' src={DULTlogo} />
                    </div>

                    {/* <div className='loadsubrowcolumn2'>
                      <img className='loaddivideLine' src={divideLineO} />
                    </div> */}

                    <div className='loadsubrowcolumn3'>
                      <img className='loadmidlogo' src={midlogo} />
                    </div>
                  </div>
                </div>

                <img className='loadlogosmall' src={logosmall} />


              </div>
            </div>

          </div>
          <div className='loadbackcolor'>

            <div className='innerrowscontainer'>




              <div className='innerrows'>

                <div className="innerrowtitle"><u>CONTENT</u></div>
                <div className="loadsubrow">
                  <div className='loadsubrowcolumn1'>
                    <img className='loadBSFlogo' src={BSFlogo} />
                  </div>
                  <div className='loadsubrowcolumn2'>
                    <img className='loaddivideLine' src={divideLine} />
                  </div>
                  <div className='loadsubrowcolumn3'>
                    <img className='loadSGlogo' src={SGlogo} />
                  </div>
                </div>
              </div>


              <div className='innerrows'>

                <div className="innerrowtitle"><u>GAME APP DEVELOPMENT</u></div>
                <div className="loadsubrow">
                  <div className='loadsubrowcolumn1'>
                    <img className='loadXarpieLabs' src={XarpieLabs} />
                  </div>

                  <div className='loadsubrowcolumn2'>
                    <img className='loaddivideLine' src={divideLine} />
                  </div>

                  <div className='loadsubrowcolumn3'>
                    <img className='loadmediaCannibal' src={mediaCannibal} />
                  </div>
                </div>
              </div>
            </div>
            <div className='loadrow5'>
              <img className='loadbgBelow' src={bgBelow} />
            </div>

          </div>
        </div>
      </div>
    </>
  )
}

export default LoadingScreen
